  flex-grow: 1; }

[class~="flex-2"] {
  flex-grow: 2; }

[class~="flex-3"] {
  flex-grow: 3; }

[class~="flex-4"] {
  flex-grow: 4; }

[class~="flex-5"] {
  flex-grow: 5; }

[class~="flex-6"] {
  flex-grow: 6; }

[class~="flex-7"] {
  flex-grow: 7; }

[class~="flex-8"] {
  flex-grow: 8; }

[class~="flex-9"] {
  flex-grow: 9; }

[class~="flex-10"] {
  flex-grow: 10; }

[class~="flex-11"] {
  flex-grow: 11; }

[class~="flex-12"] {
