  margin-right: auto; }

a[role="tab"] {
  background-image: none;
  text-shadow: none;
  border: 0;
  background-color: rgba(229, 229, 229, 0.25);
  color: #222;
  padding: 0.5em 0.75em;
  display: inline-block;
  height: 100%;
  width: 100%; }
  @media screen and (min-width: 40.0625em) {
    a[role="tab"] {
      width: auto; } }

a[aria-selected], a[role="tab"]:focus {
  background-color: #e5e5e5;
  color: #222; }

[role="tablist"] {
  padding-left: 0;
  display: flex;
  flex-flow: row nowrap; }
  [role="tablist"] li {
    width: 25%;
    margin-top: 0;
    display: inline-block;
    list-style: none;
    margin-right: 1px; }
    @media screen and (min-width: 40.0625em) {
      [role="tablist"] li {
        width: auto;
        margin-right: 2px; } }
    [role="tablist"] li:last-child {
      margin-right: 0; }

[role="tabpanel"] {
  margin-top: 0;
  background-color: #e5e5e5;
  color: #222;
  padding: 1em; }
  [role="tabpanel"] a {
    color: #222; }
    [role="tabpanel"] a:visited {
      color: #222; }
    [role="tabpanel"] a:hover, [role="tabpanel"] a:focus, [role="tabpanel"] a:active {
      color: #3c3c3c; }

[role="tabpanel"][aria-hidden="true"] {
  display: none; }

[role="tabpanel"]:focus {
  background: #eee;
  outline: thin dotted; }

/*------------------------------------*    #Tables
\*------------------------------------*/
table {
  width: 100%;
  border-collapse: collapse; }

th {
  padding: 0.75rem;
  text-align: start;
  border: 2px solid #4d4d4d; }

td {
  padding: 0.75rem;
  border: 2px solid #4d4d4d; }

/*------------------------------------*    #Tags
\*------------------------------------*/
.taxonomies {
  padding-left: 0; }
  .taxonomies li {
    margin-top: 0;
    display: inline-block;
    margin-bottom: 0.5em;
    margin-right: 1.5em; }
  .taxonomies li:last-child {
    margin-bottom: 0;
    margin-right: 0; }

/*------------------------------------*    #Thematic break
\*------------------------------------*/
/*------------------------------------*    #Payment details
