  .credit-card .credit-card__inner {
    fill: black; }
  .credit-card .credit-card__magnetic-strip {
    fill: black; }
  .credit-card .credit-card__date {
    fill: white; }
  .credit-card .credit-card__name {
    fill: white; }
  .credit-card .credit-card__cvc {
    fill: black; }
  .credit-card .credit-card__number {
    fill: white; }

